import React, { useState } from "react";
import { FaFileDownload } from "react-icons/fa";
import { ImAttachment } from "react-icons/im";
import { MdOutlineInfo, MdPersonOutline } from "react-icons/md";
import { connect } from "react-redux";
import {
  FC_GetAllTermination,
  FC_UpdateThisTermination,
  TerminationResponseType,
} from "../../actions";
import { FC_UploadFile } from "../../actions/transfer.action";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import DownloadLink from "../../components/DownloadLink/DownloadLink";
import Heading from "../../components/Heading/Heading";
import InputFile, { acceptAllEnum } from "../../components/Inputs/InputFile";
import TextArea from "../../components/Inputs/TextArea";
import TextInput from "../../components/Inputs/TextInput";
import { APP_URL, APP_URL_FILE } from "../../config/app.config";
import {
  APITerminationType,
  TerminationStatusEnum,
} from "../../types/StaffTermination";
import { DATE, isEmptyOrSpaces } from "../../utils/functions";

type HRViewTerminationProps = {
  setThiTermination: (value: APITerminationType | null) => void;
  thiTermination: APITerminationType;
  FC_UpdateThisTermination: Function;
  FC_GetAllTermination: Function;
  thisViewIsFor: "termination" | "suspension";
};

const HRViewTermination: React.FC<HRViewTerminationProps> = (props) => {
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [actionTaken, setActionTaken] = useState<TerminationStatusEnum | null>(
    null
  );
  const [formError, setFormError] = useState<{
    element:
      | "main-success"
      | "main-error"
      | "action"
      | "comment"
      | "start_date"
      | "end_date"
      | "approved_doc";
    msg: string;
  } | null>(null);
  const [responseMsg, setResponseMsg] = useState<string>("");

  const [startDate, setStartDate] = useState<string>(
    props.thisViewIsFor === "suspension" &&
      props.thiTermination.requested_to_suspend_from
      ? props.thiTermination.requested_to_suspend_from
      : ""
  );
  const [endDate, setEndDate] = useState<string>(
    props.thisViewIsFor === "suspension" &&
      props.thiTermination.requested_to_suspend_to
      ? props.thiTermination.requested_to_suspend_to
      : ""
  );

  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [upload_percentage, setUpload_percentage] = useState<number>(0);

  // const doUpload = async (uploadedFile: File):string|undefined => {
  //   let result= await FC_UploadFile(
  //     uploadedFile,
  //     (
  //       status: boolean,
  //       uploadingPercentage: number,
  //       data: string | null,
  //       msg: string
  //     ) => {
  //       setLoadingRequest(status);
  //       if (status) setUpload_percentage(uploadingPercentage);
  //       if (!status && isEmptyOrSpaces(msg) && data !== null) return data;
  //       else if (!status && !isEmptyOrSpaces(msg))
  //         setFormError({
  //           element: "main-error",
  //           msg: msg,
  //         });
  //     }
  //   );
  //   return result
  // };

  const handleResponse = async () => {
    if (!actionTaken) {
      setFormError({
        element: "action",
        msg: "Action is required!",
      });
      return setLoadingRequest(false);
    }

    // Suspension start-date validation
    if (
      props.thisViewIsFor === "suspension" &&
      actionTaken === TerminationStatusEnum.SUSPENDED &&
      isEmptyOrSpaces(startDate)
    ) {
      setFormError({
        element: "start_date",
        msg: "Required!",
      });
      return setLoadingRequest(false);
    }

    // Suspension end-date validation
    if (
      props.thisViewIsFor === "suspension" &&
      actionTaken === TerminationStatusEnum.SUSPENDED &&
      isEmptyOrSpaces(endDate)
    ) {
      setFormError({
        element: "end_date",
        msg: "Required!",
      });
      return setLoadingRequest(false);
    }

    // Document validation
    if (uploadedFile === null) {
      setFormError({
        element: "approved_doc",
        msg: "Required!",
      });
      return setLoadingRequest(false);
    }

    // Comment validation
    if (isEmptyOrSpaces(responseMsg)) {
      setFormError({
        element: "comment",
        msg: "Required!",
      });
      return setLoadingRequest(false);
    }

    let doc_title: string = "";
    await FC_UploadFile(
      uploadedFile,
      (
        status: boolean,
        uploadingPercentage: number,
        data: string | null,
        msg: string
      ) => {
        setLoadingRequest(status);
        if (status) setUpload_percentage(uploadingPercentage);
        if (!status && isEmptyOrSpaces(msg) && data !== null) doc_title = data;
        else if (!status && !isEmptyOrSpaces(msg))
          setFormError({
            element: "main-error",
            msg: msg,
          });
      }
    );

    if (!isEmptyOrSpaces(doc_title)) {
      const api_body: TerminationResponseType = {
        change_staff_status_id: props.thiTermination.change_staff_status_id,
        decision: actionTaken,
        decided_to_suspend_from:
          props.thisViewIsFor === "suspension" &&
          actionTaken === TerminationStatusEnum.SUSPENDED
            ? startDate
            : "",
        decided_to_suspend_to:
          props.thisViewIsFor === "suspension" &&
          actionTaken === TerminationStatusEnum.SUSPENDED
            ? endDate
            : "",
        decided_by_comment: responseMsg,
        mayor_letter: doc_title,
      };
      setLoadingRequest(true);
      props.FC_UpdateThisTermination(
        api_body,
        (status: boolean, msg: string) => {
          if (status)
            setFormError({
              element: "main-success",
              msg: "Response has been successful sent!",
            });
          // props.FC_GetAllTermination(
          //   "hr",
          //   (status: boolean, data: APITerminationType[] | null, msg: string) => {
          //     if (status)
          //     else
          //       setFormError({
          //         element: "main-error",
          //         msg: msg,
          //       });
          //   }
          // );
          else
            setFormError({
              element: "main-error",
              msg: msg,
            });
        }
      );

      setActionTaken(null);
      setTimeout(() => {
        setFormError(null);
        setResponseMsg("");
        // props.setThiTermination(null);
      }, 2000);
      setLoadingRequest(false);
    }
  };

  const onFileChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    setFormError(null);

    // check if the file is available
    if (e.target.files === null) {
      setUploadedFile(null);
      return setFormError({
        msg: "Please select a valid file",
        element: "approved_doc",
      });
    }

    const thisFile = e.target.files[0];

    // check the file type
    if (thisFile.type !== "application/pdf") {
      setUploadedFile(null);
      return setFormError({
        msg: "The system accept pdf only",
        element: "approved_doc",
      });
    }

    // check if the size is valued
    if (thisFile.size <= 0) {
      setUploadedFile(null);
      return setFormError({
        msg: `Invalid file, Please choose a real file`,
        element: "approved_doc",
      });
    }

    // Add image
    setUploadedFile(thisFile);
    setFormError(null);
  };

  console.log("==================props.thiTermination==================");
  console.log({ fff: props.thiTermination });
  console.log("==================props.thiTermination==================");

  return (
    <>
      {formError && formError.element === "main-success" && (
        <Alert theme="success">{formError.msg}</Alert>
      )}
      {formError && formError.element === "main-error" && (
        <Alert theme="danger">{formError.msg}</Alert>
      )}
      <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
        <div className="py-2 flex items-center">
          <div className="flex-1">
            <Heading>View {props.thisViewIsFor} progress</Heading>
          </div>
          <div className="">
            <Button
              onClick={() => props.setThiTermination(null)}
              theme="default"
            >
              Close
            </Button>
          </div>
        </div>
        {/* User info ============ */}
        <div className="bg-white p-3 shadow-sm rounded-sm">
          <div className="flex items-center space-x-2 font-semibold text-gray-900 leading-8">
            <span className="text-primary-800">
              <MdPersonOutline size={25} />
            </span>
            <span className="tracking-wide">Staff info</span>
          </div>
          <div className="text-gray-700">
            <div className="grid md:grid-cols-2 text-sm">
              <div className="grid grid-cols-2">
                <div className="px-4 py-2 font-semibold">Full Name</div>
                <div className="px-4 py-2">
                  {props.thiTermination.techer_name}
                </div>
              </div>
              <div className="grid grid-cols-2">
                <div className="px-4 py-2 font-semibold">School</div>
                <div className="px-4 py-2">
                  {props.thiTermination.school_name}
                </div>
              </div>
              <div className="grid grid-cols-2">
                <div className="px-4 py-2 font-semibold">School code</div>
                <div className="px-4 py-2">
                  {props.thiTermination.school_code}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* User comment ============ */}
        <div className="bg-white p-3 border rounded">
          <div className="flex items-center px-2 py-1 bg-gray-200 rounded">
            <MdOutlineInfo className="w-8" />
            <p>{props.thiTermination.reason_name}</p>
          </div>
          <div className="flex flex-col mt-3">
            <span className="text-sm text-gray-500">Comment:</span>
            <p className="text-base">
              {props.thiTermination.requested_by_comment}
            </p>
          </div>
          {props.thisViewIsFor === "suspension" && (
            <div className="flex flex-row items-center gap-2">
              <span className="text-sm text-gray-500">Start date:</span>
              <p className="text-base">
                {props.thiTermination.requested_to_suspend_from &&
                !isEmptyOrSpaces(props.thiTermination.requested_to_suspend_from)
                  ? DATE(props.thiTermination.requested_to_suspend_from)
                  : "-"}
              </p>
            </div>
          )}
          {props.thisViewIsFor === "suspension" && (
            <div className="flex flex-row items-center gap-2">
              <span className="text-sm text-gray-500">End date:</span>
              <p className="text-base">
                {props.thiTermination.requested_to_suspend_to &&
                !isEmptyOrSpaces(props.thiTermination.requested_to_suspend_to)
                  ? DATE(props.thiTermination.requested_to_suspend_to)
                  : "-"}
              </p>
            </div>
          )}
          <div className="flex flex-row items-center gap-2">
            <span className="text-sm text-gray-500">Request date:</span>
            <p className="text-base">
              {DATE(props.thiTermination.requested_by_date)}
            </p>
          </div>
        </div>

        {/* Feedback ============ */}
        {props.thiTermination.status !== TerminationStatusEnum.PENDING &&
          props.thiTermination.status !==
            TerminationStatusEnum.PENDING_SUSPENSION && (
            <section>
              <span className="text-sm text-gray-500">Feedback:</span>
              <div className="bg-white p-3 border rounded">
                <div className="flex items-center gap-2">
                  <span className="text-sm text-gray-500">Decision:</span>
                  <p className="text-base">{props.thiTermination.status}</p>
                </div>
                {props.thisViewIsFor === "suspension" && (
                  <div className="flex flex-col">
                    <span className="text-sm text-gray-500">Start date:</span>
                    <p className="text-base">
                      {props.thiTermination.decided_to_suspend_from &&
                      !isEmptyOrSpaces(
                        props.thiTermination.decided_to_suspend_from
                      )
                        ? DATE(props.thiTermination.decided_to_suspend_from)
                        : "-"}
                    </p>
                  </div>
                )}
                {props.thisViewIsFor === "suspension" && (
                  <div className="flex flex-col">
                    <span className="text-sm text-gray-500">End date:</span>
                    <p className="text-base">
                      {props.thiTermination.decided_to_suspend_to &&
                      !isEmptyOrSpaces(
                        props.thiTermination.decided_to_suspend_to
                      )
                        ? DATE(props.thiTermination.decided_to_suspend_to)
                        : "-"}
                    </p>
                  </div>
                )}
                {props.thiTermination.status !==
                  TerminationStatusEnum.REJECTED &&
                  props.thiTermination.mayor_letter && (
                    <DownloadLink
                      linkFor="uploaded"
                      first_id={props.thiTermination.mayor_letter}
                      last_id={""}
                      title={"Confirmation letter"}
                    />
                  )}
                <div className="flex flex-col">
                  <span className="text-sm text-gray-500">Comment:</span>
                  <p className="text-base">
                    {props.thiTermination.decided_by_comment}
                  </p>
                </div>
                <div className="flex flex-row items-center gap-2">
                  <span className="text-sm text-gray-500">Done on:</span>
                  <p className="text-base">
                    {props.thiTermination.decided_by_date
                      ? DATE(props.thiTermination.decided_by_date)
                      : "-"}
                  </p>
                </div>
              </div>
            </section>
          )}

        {/* FOOTER INITIAL ========================================== */}
        {!actionTaken && (
          <section className="flex items-center justify-between bg-gray-50 rounded mt-3">
            {/* SHOWING STATUS ===================================== */}
            <div className="mx-4 my-4">
              <span
                className={`${
                  props.thiTermination.status ===
                    TerminationStatusEnum.PENDING ||
                  props.thiTermination.status ===
                    TerminationStatusEnum.PENDING_SUSPENSION
                    ? "text-yellow-700"
                    : props.thiTermination.status ===
                      TerminationStatusEnum.REJECTED
                    ? "text-red-700"
                    : "text-green-700"
                } italic font-semibold`}
              >
                {props.thiTermination.status ===
                  TerminationStatusEnum.PENDING ||
                props.thiTermination.status ===
                  TerminationStatusEnum.PENDING_SUSPENSION
                  ? "Pending"
                  : props.thiTermination.status ===
                    TerminationStatusEnum.REJECTED
                  ? "Canceled"
                  : "Confirmed"}
              </span>
            </div>

            {(props.thiTermination.status === TerminationStatusEnum.PENDING ||
              props.thiTermination.status ===
                TerminationStatusEnum.PENDING_SUSPENSION) && (
              <div className="flex items-center gap-2 px-2">
                <DownloadLink
                  first_id={props.thiTermination.change_staff_status_id}
                  last_id={props.thiTermination.teacher_id}
                  title={
                    props.thisViewIsFor === "suspension"
                      ? "Suspension letter"
                      : "Termination letter"
                  }
                  linkFor={props.thisViewIsFor}
                />
                <Button
                  theme="danger"
                  type="button"
                  onClick={() => {
                    setActionTaken(TerminationStatusEnum.REJECTED);
                  }}
                >
                  Reject
                </Button>
                <Button
                  type="button"
                  theme="success"
                  onClick={() => {
                    setActionTaken(
                      props.thisViewIsFor === "termination"
                        ? TerminationStatusEnum.TERMINATED
                        : TerminationStatusEnum.SUSPENDED
                    );
                  }}
                >
                  Accept
                </Button>
              </div>
            )}
          </section>
        )}
        {/* END FOOTER INITIAL ========================================== */}

        {actionTaken && (
          <section className="my-3 overflow-x-auto">
            {upload_percentage !== 0 && (
              <div className="p-2">
                <div className="bg-accent-400 appearance-none rounded w-full border-bg-accent-800 border-2 transition-all duration-500 ease-in-out">
                  <div
                    className="bg-accent-800 rounded-sm text-white text-center py-2 transition-all duration-500 ease-in-out"
                    style={{
                      width: upload_percentage + "%",
                    }}
                  >
                    <span className="text-gray-700 mx-1">Uploading...</span>
                  </div>
                </div>
              </div>
            )}

            <div className="italic text-gray-700 text-base mx-5">
              Selected decision:{" "}
              <span
                className={`font-semibold text-lg ${
                  actionTaken === TerminationStatusEnum.TERMINATED ||
                  actionTaken === TerminationStatusEnum.SUSPENDED
                    ? "text-blue-700"
                    : "text-yellow-800"
                }`}
              >
                {actionTaken === TerminationStatusEnum.TERMINATED ||
                actionTaken === TerminationStatusEnum.SUSPENDED
                  ? "Approved"
                  : "Rejected"}
              </span>
            </div>
            {/* right part */}
            <div className="flex flex-col md:flex-row items-end md:items-start gap-1 w-full">
              <div className="w-full px-2">
                {props.thisViewIsFor === "suspension" &&
                  actionTaken === TerminationStatusEnum.SUSPENDED && (
                    <div className="flex flex-wrap">
                      <div className="w-full lg:w-1/2 py-1 px-3">
                        <TextInput
                          disabled={loadingRequest}
                          label="Start date"
                          name="start_date"
                          type="date"
                          value={startDate}
                          placeholder={"Suspension starting date"}
                          className="p-2"
                          onChange={(e) => {
                            setStartDate(e.target.value);
                            setFormError(null);
                          }}
                          error={
                            formError && formError.element === "start_date"
                              ? formError.msg
                              : ""
                          }
                        />
                      </div>

                      <div className="w-full lg:w-1/2 py-1 px-3">
                        <TextInput
                          disabled={loadingRequest}
                          label="End date"
                          name="end_date"
                          type="date"
                          value={endDate}
                          placeholder={"Suspension ending date"}
                          className="p-2"
                          onChange={(e) => {
                            setEndDate(e.target.value);
                            setFormError(null);
                          }}
                          error={
                            formError && formError.element === "end_date"
                              ? formError.msg
                              : ""
                          }
                        />
                      </div>
                    </div>
                  )}
                <div className="w-full py-1 px-2">
                  <InputFile
                    disabled={loadingRequest}
                    label="Suspension document"
                    name="approved_doc"
                    className="p-2"
                    onChange={(e) => onFileChange(e)}
                    error={
                      formError && formError.element === "approved_doc"
                        ? formError.msg
                        : ""
                    }
                    type={"file"}
                    accept={acceptAllEnum.PDF}
                  />
                </div>
                <div className="w-full py-1 px-2">
                  <TextArea
                    disabled={loadingRequest}
                    label="Comment"
                    name="comment"
                    value={responseMsg}
                    placeholder={"Reason of your decision..."}
                    className="p-2"
                    onChange={(e) => {
                      setResponseMsg(e.target.value);
                      setFormError(null);
                    }}
                    error={
                      formError && formError.element === "comment"
                        ? formError.msg
                        : ""
                    }
                  />
                </div>
                <div className="flex items-center gap-1 max-w-full px-2 float-right">
                  <Button
                    theme="default"
                    onClick={() => {
                      setResponseMsg("");
                      setActionTaken(null);
                      setFormError(null);
                    }}
                    disabled={loadingRequest}
                  >
                    <span>Back</span>
                  </Button>
                  <Button
                    disabled={loadingRequest}
                    theme="primary"
                    onClick={() =>
                      window.confirm(
                        `Are you sure you want to ${
                          actionTaken === TerminationStatusEnum.TERMINATED
                            ? "terminate this staff?"
                            : actionTaken === TerminationStatusEnum.SUSPENDED
                            ? "suspend this staff?"
                            : "rejected this request?"
                        }`
                      ) && handleResponse()
                    }
                  >
                    {loadingRequest ? (
                      <div className="">Loading...</div>
                    ) : (
                      <span>Confirm</span>
                    )}
                  </Button>
                </div>
              </div>
            </div>
            {/* end right part */}
          </section>
        )}
      </DashboardContainer>
    </>
  );
};

export default connect(null, {
  FC_UpdateThisTermination,
  FC_GetAllTermination,
})(HRViewTermination);
