import React, { Component } from "react";
import Button from "../../components/Buttons/Button";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import TableLoading from "../../components/TableLoading/TableLoading";
import {
  APITerminationType,
  TerminationStatusEnum,
} from "../../types/StaffTermination";
import { DATE, isEmptyOrSpaces, search } from "../../utils/functions";

type SuspensionItemsProps = {
  suspensionItems: APITerminationType[] | null;
  loading: boolean;
  setThisTermination: (value: APITerminationType | null) => void;
};
type SuspensionItemsState = {
  search: string;
  thisStatus: TerminationStatusEnum | "all";
};

class SuspensionItems extends Component<
  SuspensionItemsProps,
  SuspensionItemsState
> {
  constructor(props: SuspensionItemsProps) {
    super(props);

    this.state = {
      search: "",
      thisStatus: "all",
    };
  }

  setThisStatus = (value: TerminationStatusEnum | "all") => {
    this.setState({ thisStatus: value });
  };

  filterTerminations = (
    allData: APITerminationType[],
    search: TerminationStatusEnum | "all"
  ): APITerminationType[] => {
    let result: APITerminationType[] = allData;
    result = allData.filter(
      (item) =>
        item.status !== TerminationStatusEnum.PENDING &&
        item.status !== TerminationStatusEnum.TERMINATED &&
        item.requested_to_suspend_from !== null
    );
    if (search !== "all") {
      result = result.filter((item) => item.status === search);
    }
    return result;
  };

  render() {
    return (
      <div className="mt-3">
        <div className="border border-t mt-2 mb-1 flex items-center flex-col-reverse sm:flex-row pl-3 pr-2 rounded">
          <div className="w-full sm:flex-1 pr-2">
            <TextInput
              error=""
              label="Search"
              name=""
              type="search"
              onChange={(e) => this.setState({ search: e.target.value })}
              placeholder="Search by name, reason, date, status"
              value={this.state.search}
              size="sm"
              disabled={
                this.props.suspensionItems === null ||
                this.filterTerminations(
                  this.props.suspensionItems,
                  this.state.thisStatus
                ).length === 0
              }
            />
          </div>
          <div className="w-full sm:w-auto">
            <InputSelect
              data={[
                { title: "All", value: "all" },
                {
                  title: "Pending",
                  value: TerminationStatusEnum.PENDING_SUSPENSION,
                },
                {
                  title: "Suspended",
                  value: TerminationStatusEnum.SUSPENDED,
                },
                { title: "Rejected", value: TerminationStatusEnum.REJECTED },
              ]}
              error=""
              name="select_status"
              onChange={(e) =>
                this.setState({
                  thisStatus: e.target.value as TerminationStatusEnum | "all",
                })
              }
              label="Status"
              placeholder="Select status"
              value={this.state.thisStatus}
              size="sm"
            />
          </div>
          {/* {this.props.suspensionItems && (
            <div className="p-2 px-3 ml-3 bg-accent-800 bg-opacity-30 rounded">
              <label className="">Results:</label>
              <h4 className="text-2xl -mt-1 font-bold">
                {
                  this.filterTerminations(
                    this.props.suspensionItems,
                    this.state.thisStatus
                  ).length
                }
              </h4>
            </div>
          )} */}
        </div>
        <div className="my-3 w-full overflow-x-auto">
          <table className="w-full text-left table-auto">
            <thead>
              <tr>
                <th className="p-2">#</th>
                <th className="p-2">Names</th>
                <th className="p-2">School</th>
                <th className="p-2">Reason</th>
                <th className="p-2">Date</th>
                <th className="p-2">Status</th>
                <th className="p-2"></th>
              </tr>
            </thead>
            <tbody>
              {this.props.loading ? (
                <TableLoading cols={6} />
              ) : this.props.suspensionItems === null ||
                this.filterTerminations(
                  this.props.suspensionItems,
                  this.state.thisStatus
                ).length <= 0 ? (
                <tr>
                  <td
                    colSpan={8}
                    className="p-8 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                  >
                    <Heading size="md" className="opacity-60 font-light">
                      No results found
                    </Heading>
                  </td>
                </tr>
              ) : (
                search(
                  this.filterTerminations(
                    this.props.suspensionItems,
                    this.state.thisStatus
                  ),
                  this.state.search
                ).map((req: APITerminationType, i: number) => {
                  return (
                    <tr
                      className="group border-b border-t rounded hover:bg-accent-800 hover:bg-opacity-20 cursor-pointer"
                      onClick={() => this.props.setThisTermination(req)}
                      key={i + req.change_staff_status_id}
                    >
                      <td className="p-2">{i + 1}</td>
                      <td className="p-2">{req.techer_name}</td>
                      <td className="p-2">{req.school_name}</td>
                      <td className="p-2">{req.reason_name}</td>
                      <td className="p-2">
                        {req.status === TerminationStatusEnum.PENDING_SUSPENSION
                          ? DATE(req.requested_by_date)
                          : req.decided_by_date && DATE(req.decided_by_date)}
                      </td>
                      <td className="p-2">{req.status}</td>
                      <td className="p-1 text-center">
                        <Button
                          // className="border border-accent-900 bg-white text-accent-900 group-hover:text-white group-hover:bg-accent-900"
                          onClick={() => this.props.setThisTermination(req)}
                        >
                          View
                        </Button>
                      </td>
                    </tr>
                  );
                })
              )}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default SuspensionItems;
