import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic, FC_GetAllTermination } from "../../actions";
import Alert from "../../components/Alert/Alert";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { TerminationReducerType } from "../../reducers/termination.reducer";
import { APITerminationType } from "../../types/StaffTermination";
import { isEmptyOrSpaces } from "../../utils/functions";
import HRViewTermination from "./HRViewTermination";
import SuspensionItems from "./SuspensionItems";
import TerminationItems from "./TerminationItems";

export enum HRTabsType {
  terminationTab = "Staff Termination",
  suspensionTab = "Staff Suspension",
}

type HRTerminationProps = {
  auth: Auth;
  basic: Basic;
  terminations: TerminationReducerType;
  FC_GetAllTermination: Function;
  thisPage: HRTabsType;
};

type HRTerminationState = {
  loading: boolean;
  error: {
    target: "requests-error" | "main-success" | "main-error" | null;
    msg: string;
  };
  thisTermination: APITerminationType | null;
};

class HRTermination extends Component<HRTerminationProps, HRTerminationState> {
  constructor(props: HRTerminationProps) {
    super(props);

    this.state = {
      loading: true,
      error: {
        target: null,
        msg: "",
      },
      thisTermination: null,
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setError = (value: {
    target: "requests-error" | "main-success" | "main-error" | null;
    msg: string;
  }) => {
    this.setState({ error: value });
  };

  setThisTermination = (value: APITerminationType | null) => {
    this.setState({ thisTermination: value });
  };

  getTerminationById = (
    allTermination: APITerminationType[],
    terminationId: string
  ) => {
    return allTermination.find(
      (item) => item.change_staff_status_id === terminationId
    );
  };

  componentDidMount = () => {
    this.props.FC_GetAllTermination(
      "hr",
      (status: boolean, data: APITerminationType[] | null, msg: string) => {
        this.setLoading(status);
        if (!status && !isEmptyOrSpaces(msg))
          this.setError({
            target: "main-error",
            msg: msg,
          });
      }
    );
  };

  render() {
    if (this.state.loading)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success">{this.state.error.msg}</Alert>
        )}
        {this.state.error.target === "main-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}
        {this.state.thisTermination === null ? (
          <DashboardContainer>
            <Heading>{this.props.thisPage}</Heading>
            {/* <div className="flex flex-row items-center mt-2">
              {Object.values(HRTabsType).map((item, i) => (
                <div
                  key={i}
                  onClick={() =>
                    !this.state.loading && this.setSelectedTab(item)
                  }
                  className={`animate__animated ${
                    this.state.selectedTab === item
                      ? "bg-accent-400 border-accent-900 text-accent-900 animate__zoomIn animate__faster"
                      : "bg-white border-white text-gray-600"
                  } hover:bg-accent-100 px-4 py-2 ${
                    this.state.loading ? "cursor-wait" : "cursor-pointer"
                  } font-bold border-b-4 hover:border-accent-900 hover:text-accent-900 rounded-t mt-1 mx-1`}
                >
                  {item}
                </div>
              ))}
            </div> */}
            {this.props.thisPage === HRTabsType.terminationTab ? (
              <TerminationItems
                terminations={this.props.terminations.allTermination}
                loading={this.state.loading}
                setThisTermination={this.setThisTermination}
              />
            ) : (
              <SuspensionItems
                suspensionItems={this.props.terminations.allTermination}
                loading={this.state.loading}
                setThisTermination={this.setThisTermination}
              />
            )}
          </DashboardContainer>
        ) : (
          <HRViewTermination
            setThiTermination={this.setThisTermination}
            thiTermination={
              this.getTerminationById(
                this.props.terminations.allTermination!,
                this.state.thisTermination.change_staff_status_id
              )!
            }
            thisViewIsFor={
              this.props.thisPage === HRTabsType.terminationTab
                ? "termination"
                : "suspension"
            }
          />
        )}
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  terminations,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  terminations: TerminationReducerType;
} => {
  return { auth: auth, basic: basic, terminations };
};
export default connect(mapStateToProps, { FC_GetAllTermination })(
  HRTermination
);
