export type FT_GetAllTransfer = (
  callBack: (
    status: boolean,
    data: TransferRequestType[] | null,
    msg: string
  ) => void
) => void;
export const FC_GetAllTransfers = (
  callBack: (
    status: boolean,
    data: TransferRequestType[] | null,
    msg: string
  ) => void
) => {
  callBack(true, null, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<TransferRequestType[]>(
        `${APP_URL}/teachertransfers/requesteddde`
      );
      console.log("====================================");
      console.log({ getAllTransfers: res });
      console.log("====================================");

      // * save data
      dispatch<GetAllTransferAction>({
        type: ActionTypes.GET_ALL_TRANSFER,
        payload: res.data,
      });
      callBack(false, res.data, "");
    } catch (error) {
      console.log(error);
      callBack(false, null, errorToText(error));
      //   callBack(
      //     false,
      //     errorToText(error, () => {})
      //   );
    }
  };
};

