// file upload function
export const FC_UploadFile = async (
  fileToUpload: File,
  callBack: (
    status: boolean,
    uploadingPercentage: number,
    data: string | null,
    msg: string
  ) => void
) => {
  // Validation
  let number_percentage: number = 0;
  callBack(true, number_percentage, null, "");
  try {
    setAxiosToken();
    const formData = new FormData();
    formData.append("teacher_supporting_document", fileToUpload);
    const res = await axios.post<string>(
      `${APP_URL}/teachertransfers/uploadSupportingDocument`,
      formData,
      {
        onUploadProgress: (progressEvent: any) => {
          number_percentage =
            (+progressEvent?.loaded / +progressEvent.total) * 100;
          callBack(true, number_percentage, null, "");
        },
      }
    );
    console.log("Uploaded document: ", res.data);
    callBack(false, number_percentage, res.data, "");
    // console.log("Questions: ", res);
  } catch (error: any) {
    console.error(error);
    callBack(false, number_percentage, "", errorToText(error));
  }
};

