export const FC_RespondLeave = (
  selectedUserId: string,
  comment: string,
  selectedFile: File,
  decision: "REJECTED" | "APPROVED",
  callBack: (status: boolean, response_error: string) => void
) => {
  callBack(true, "");
  let doc_title: string = "";
  // FC_HandleUpload(selectedFile);

  return async (dispatch: Dispatch) => {
    callBack(true, "");
    await FC_UploadFile(
      selectedFile,
      (
        status: boolean,
        uploadingPercentage: number,
        data: string | null,
        msg: string
      ) => {
        callBack(status, "");
        console.log("==================&&&&&&&&&&&&&==================");
        console.log({ data });
        console.log("==================&&&&&&&&&&&&&==================");
        if (!status && isEmptyOrSpaces(msg) && data !== null) doc_title = data;
        else if (!status && !isEmptyOrSpaces(msg)) callBack(false, msg);
        return;
      }
    );
    if (!isEmptyOrSpaces(doc_title)) {
      callBack(true, "");
      const data = {
        teacher_transfer_id: selectedUserId,
        outgoing_status: decision,
        outgoing_dde_comment: comment,
        mayor_letter: doc_title,
      };

      console.log("==================*********************==================");
      console.log({ doc_title });
      console.log("==================*********************==================");

      try {
        setAxiosToken();
        const res = await axios.post<string>(
          `${APP_URL}/teachertransfers/outgoing`,
          data
        );
        console.log("Uploaded document: ", res.data);
        dispatch<RespondToLeaveAction>({
          type: ActionTypes.RESPOND_TO_LEAVE,
          payload: data,
        });
        callBack(false, "");
        // console.log("Questions: ", res);
      } catch (error: any) {
        console.error(error);
        callBack(false, errorToText(error));
      }
    }
  };
};

export type FT_RespondTransfer = (
  teacher_transfer_id: string,
  approved_school: SchoolInfoType,
  decision: "REJECTED" | "APPROVED",
  comment: string,
  callBack: (status: boolean, response_error: string) => void
) => void;
export const FC_RespondTransfer = (
  teacher_transfer_id: string,
  approved_school: SchoolInfoType,
  decision: "REJECTED" | "APPROVED",
  comment: string,
  callBack: (status: boolean, response_error: string) => void
) => {
  callBack(false, "");
  return async (dispatch: Dispatch) => {
    callBack(true, "");
    const data = {
      teacher_transfer_id: teacher_transfer_id,
      requested_status: decision,
      approved_school_id: approved_school.school_code,
      requested_comment: comment,
    };

    try {
      setAxiosToken();
      const res = await axios.post<string>(
        `${APP_URL}/teachertransfers/incoming`,
        data
      );
      console.log("FC_RespondTransfer document: ", res.data);
      dispatch<RespondToTransferAction>({
        type: ActionTypes.RESPOND_TO_TRANSFER,
        payload: {
          teacher_transfer_id: teacher_transfer_id,
          requested_status: decision,
          approved_school: approved_school,
          requested_comment: comment,
        },
      });
      callBack(false, "");
      // console.log("Questions: ", res);
    } catch (error: any) {
      console.error(error);
      // callBack(false, number_percentage, "", errorToText(error));
      callBack(false, errorToText(error));
    }
  };
};

