import React from "react";
import { ImAttachment } from "react-icons/im";
import { APP_URL, APP_URL_FILE } from "../../config/app.config";

type DownloadLinkProps = {
  linkFor: "uploaded" | "termination" | "suspension" | "transfer";
  first_id: string | number;
  last_id: string | number;
  title: string;
};

const DownloadLink: React.FC<DownloadLinkProps> = (props) => {
  const this_URL: string =
    props.linkFor === "uploaded"
      ? `${APP_URL_FILE}/${props.first_id}`
      : `${APP_URL}/generateletter/${props.linkFor}/${props.first_id}/${props.last_id}`;
  return (
    <a
      href={this_URL}
      className="py-2 px-4 text-md w-max font-bold rounded bg-primary-800 hover:bg-opacity-90 text-white flex items-center gap-2"
      target="_blank"
      rel="noreferrer"
    >
      <ImAttachment />
      <span>{props.title}</span>
    </a>
  );
};

export default DownloadLink;
