import React from "react";

type TableLoadingProps = {
  cols: number;
};
const TableLoading: React.FC<TableLoadingProps> = (props) => {
  const displayCols = (): JSX.Element[] => {
    let colsArray: number[] = [];
    for (let index = 0; index < props.cols; index++) {
      colsArray.push(index);
    }
    let result = colsArray.map((element) => {
      return (
        <td className="p-3 text-center whitespace-nowrap" key={element}>
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      );
    });

    return result;
  };
  return (
    <>
      <tr className="border-b border-gray-200" key={228}>
        {displayCols()}
      </tr>
      <tr className="border-b border-gray-200" key={22648}>
        {displayCols()}
      </tr>
      <tr className="border-b border-gray-200" key={229768}>
        {displayCols()}
      </tr>
    </>
  );
};

export default TableLoading;
