export enum acceptAllEnum {
  ALL = "",
  IMAGES = "image/*",
  EXCEL = ".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel",
  PDF = "application/pdf",
}

type InputFileProps = {
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
  error: string;
  label: string;
  disabled?: boolean;
  type: "file" | "image";
  name: string;
  accept?: acceptAllEnum;
  multiple?: boolean;
  className?: string;
  size?: InputSizeType;
  Input_className?: string;
  Label_className?: string;
  InputContainer_className?: string;
  MainContainer_className?: string;
  labelColor?: "text-gray-300" | "text-primary-900 dark:text-primary-100";
};

const InputFile: FC<InputFileProps> = ({
  onChange,
  error,
  label,
  multiple,
  disabled = false,
  accept = acceptAllEnum.ALL,
  name,
  type,
  Input_className = "",
  Label_className = "",
  InputContainer_className = "",
  MainContainer_className = "",
  size = "md",
  labelColor = "text-primary-900 dark:text-primary-100",
}): ReactElement => {
  /* function body */
  return (
    <>
      <div className={`w-full my-2 ${MainContainer_className}`}>
        {label !== "" && (
          <label
            htmlFor="any-form"
            className={`${labelColor} ${Label_className}`}
          >
            {label}
          </label>
        )}
        <div className={`rounded relative ${InputContainer_className}`}>
          <input
            name={name}
            multiple={multiple}
            accept={accept}
            type={type}
            onChange={onChange}
            className={`
                w-full text-lg rounded border-2 
                ${InputSize(size)}
                ${
                  error !== ""
                    ? "border-red-500 dark:text-white bg-red-100 dark:bg-red-900 dark:bg-opacity-30"
                    : "border-primary-800 dark:text-white bg-white dark:bg-primary-900 "
                }
                px-3 ${Input_className}`}
            disabled={disabled}
          />

          {error !== "" && (
            <p className="text-sm text-red-600 dark:text-red-500">{error}</p>
          )}
        </div>
      </div>
    </>
  );
};
export default InputFile;
